import { Component, OnInit } from '@angular/core';
import { AlertController, ModalController, PopoverController, ToastController } from '@ionic/angular';
import { OpcionesCDOComponent } from 'src/app/components/opciones-cdo/opciones-cdo.component';
import { ApiService } from 'src/app/Services/Api/Api.service';
import * as _ from 'underscore'
import * as moment from 'moment-timezone'
import { ActivatedRoute } from '@angular/router';
import { LoadingService } from 'src/app/Services/Utilities/Loading.service';
import { UsariosInverComponent } from 'src/app/components/Inverpack/usarios-inver/usarios-inver.component';
import { ToastService } from 'src/app/Services/Utilities/Toast.service';

@Component({
  selector: 'app-mantenimientos',
  templateUrl: './mantenimientos.page.html',
  styleUrls: ['./mantenimientos.page.scss'],
})
export class MantenimientosPage implements OnInit {
  actividades = [];
  unicos = [];

  filtro = 'Todos'
  txt = '';

  cargaActividades;
  tkn;
  btn;

  padres = [];

  click = false;

  constructor(
    private api: ApiService,
    private popover: PopoverController,
    private route: ActivatedRoute,
    private modalCtrl: ModalController,
    private alert: AlertController,
    private toast: ToastController,
    private loading: LoadingService,
    private ts: ToastService
  ) { }

  ngOnInit() {
    setInterval(() => {
      this.cargarData()
    }, 60000);
  }

  getInfo(guid) {
    return new Promise((resolve, reject) => {
      this.api.getActivitieInfo(this.tkn, guid).subscribe((data) => {
        if (data) {

          resolve(data)
        } else {
          resolve(false)
        }
      })
    })
  }


  ionViewWillEnter() {

    this.route.queryParams.subscribe((param) => {
      console.log(param);
      this.tkn = param.tkn;
      this.btn = param.btn;
      this.cargarData()
    })



  }

  // HreSCg1x3x BIOMEDICOS 2

  createOnlyActivity(guid, data) {

    return new Promise((resolve, reject) => {


      this.api.aceptActivity({
        AccessToken: this.tkn,
        FormGUID: 'HreSCg1x3x',
        LocationGUID: '',
        AssetGUID: '',
        UserGUID: guid,
        Duration: "60",
        DispachDateTime: moment().add(1, 'minutes').format('YYYY-MM-DD HH:mm'),
        Values: JSON.stringify([]),
        ParentGUID: data.GUID,
        ActivityGUID: '',
        CompanyStatusGUID: 'B2A827E7-9D30-4C59-B505-F527C787322A'
      }).subscribe(async (dat: any) => {
        console.log(dat)

        if (dat.Status === 'OK') {

          resolve(true)

        } else {
          resolve(false)
        }
      })
    })

  }

  async assign(acti) {
    if (!this.click) {
      this.click = true;
      this.loading.createLoading('Consultando si la solciitud ya tiene orden...')
      const childs: any = await this.getChild(acti.GUID)

      if (childs.length > 0) {
        this.loading.cancelLoading();
        this.ts.newCreatedToast('La solicitud ya tiene una orden creada', true);
        this.cargarData();
        return;
      }



      const modal = await this.modalCtrl.create({
        component: UsariosInverComponent,
        componentProps: {
          tkn: this.tkn,
          acti
        }
      })

      await modal.present();
      this.click = false;

      const close = await modal.onWillDismiss();

      if (close) {
        if (close.data) {

          await this.cargarData();
        }
      }
    }
  }

  async tomar(acti, i) {
    console.log(acti)



    const alert = await this.alert.create({
      message: 'Seleccione nombre del tècnico',
      header: 'Atención',
      inputs: [
        {
          type: 'radio',
          value: 'B0C8BA3F-757C-44BC-A363-02F4D7B26BA0',
          label: 'DIEGO CHAMORRO'
        },
        {
          type: 'radio',
          value: 'D1016D99-0FAA-4C96-B811-77175E9A1B34',
          label: 'ROLANDO SALAZAR ERAZO'
        },
        {
          type: 'radio',
          value: '2219E883-62EE-4129-9B2D-53778716339C',
          label: 'SERGIO DAVID ELVIRA ELVIRA'
        },



      ],
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',

        },
        {
          text: 'Aceptar',
          handler: async (data) => {
            console.log(data)
            if (data) {

              this.loading.createLoading('Tomando actividad')

              this.api.childs({
                AccessToken: this.tkn,
                ParentGUID: acti.GUID
              }).subscribe(async (dat: any) => {
                const form = dat.filter((dt) => dt.SurveyGUID == 'HreSCg1x3x');

                if (form.length == 0) {

                  const resp = await this.createOnlyActivity(data, acti)


                  if (!resp) {
                    const toast = await this.toast.create({
                      message: 'No se pudo tomar la actividad',
                      header: 'Error',
                      duration: 3000
                    })

                    await toast.present();
                  } else {
                    this.cargarData();
                  }

                  this.loading.cancelLoading();
                } else {
                  this.loading.cancelLoading();
                  const toast = await this.toast.create({
                    message: 'La actividad ya ha sido tomada por otro usuario',
                    header: 'Atención!',
                    duration: 3000
                  })

                  await toast.present();
                  this.actividades.splice(i, 1)
                }

              }, async (err) => {
                const toast = await this.toast.create({
                  message: 'No se pudo tomar la actividad',
                  header: 'Error',
                  duration: 3000
                })

                await toast.present();
                this.loading.cancelLoading();
              })





            } else {



              const toast = await this.toast.create({
                message: 'Debe seleccionar un usuario',
                header: 'Mensaje',
                duration: 3000
              })

              await toast.present();

            }

          }
        }
      ]
    })

    await alert.present();




  }

  async getChild(guid) {

    return new Promise((resolve, reject) => {

      this.api.childs({
        AccessToken: this.tkn,
        ParentGUID: guid
      }).subscribe(async (dat: any) => {

        if (dat.length > 0) {

          const miData: any = await this.getInfo(dat[0].GUID)

          resolve(miData)

        } else {
          resolve([])
        }



      }, (err) => {
        resolve([])
      })
    })

  }



  reenviar(item) {

    this.loading.createLoading('Reenviando Orden')

    this.api.changeDispath({
      tkn: this.tkn,
      user: item.childs.AssignedTo_GUID,
      status: item.childs.CompanyStatusGUID,
      date: moment().add(2, 'minutes').format('YYYY-MM-DD HH:mm'),
      guid: item.childs.GUID
    }).subscribe(async (data: any) => {
      if (data) {
        const toas = await this.toast.create({
          message: 'Orden reenviada',
          duration: 2000
        })

        await toas.present();
      }

      this.loading.cancelLoading()
    }, (err) => {
      this.loading.cancelLoading()
    })

  }

  async dataSolJefe() {
    return new Promise((resolve, reject) => {
      this.api.getActivities2(this.tkn, moment().subtract(3, 'days').format('YYYY-MM-DD HH:mm'), moment().format('YYYY-MM-DD HH:mm'), '02CEE670-587E-49CA-A2CC-C10B1D519F65').subscribe(async (data: any) => {
        resolve(data)
      }, (err) => resolve([]))

    })
  }

  cargarData() {
    this.cargaActividades = false;
    this.actividades = [];
    this.api.getActivities2(this.tkn, moment().subtract(3, 'days').format('YYYY-MM-DD HH:mm'), moment().format('YYYY-MM-DD HH:mm'), 'BAB8F522-721F-4E63-BF9F-C99F2CB78AC6').subscribe(async (data: any) => {




      const data2 = await this.dataSolJefe();
      console.log(data, 'MI DATA', data2)
      _.each(data2, (dat: any, i) => {
        dat.form = 'jefe';
        data.push(dat)
      })
      for (const item of data) {

        const miData: any = await this.getInfo(item.GUID)

        if (miData) {

          const habilitar = miData.Values.filter((l) => l.apiId == 'HABILITAR');

          if (item.form && habilitar.length == 0) {
            break;

          }

          if (item.form && habilitar[0].Value != 'SI') {
            break;
          }
          miData.jefe = item.form ? true : false;
          miData.color = this.color(miData.CompanyStatusName);
          miData.icolor = this.color(miData.CompanyStatusName, true);

          if (miData.CompanyStatusName == 'AREA DE MANTENIMIENTO') {
            miData.prioridad = 1;
          } else if (miData.CompanyStatusName == 'SOLICITUD ENVIADA AL TECNICO') {
            miData.childs = await this.getChild(miData.GUID)

            miData.prioridad = 2;
          } else {
            miData.prioridad = 3;
          }

          if (miData.childs) {


            if (miData.childs.CompanyStatusName != 'MANTENIMIENTO FINALIZADO') {
              var inicio = moment(miData.childs.CreatedOn).format('YYYY-MM-DD HH:mm:ss')
              var fin = moment().format('YYYY-MM-DD HH:mm:ss')
              var dur = moment(fin).diff(moment(inicio), 'seconds');

              if (miData.childs.Values) {

                console.log(miData.childs.Values, 'hola', miData)
                const valprioridad = miData.childs.Values.filter((cam) => cam.apiId == 'PRIORIDAD');
                let prio = '';
                let tiempo = 0;

                if (valprioridad.length > 0) {
                  prio = valprioridad[0].Value != '' ? valprioridad[0].Value : 'BAJA'
                } else {
                  prio = 'BAJA'
                }



                if (prio == 'ALTA') {
                  tiempo = (60 * 60 * 12) - dur;
                  if (dur > 43200) {
                    miData.showTimer = true;
                  }
                } else if (prio == 'MEDIA') {
                  tiempo = (60 * 60 * 24) - dur;
                  if (dur > 86400) {
                    miData.showTimer = true;
                  }
                } else if (prio == 'BAJA') {
                  tiempo = (60 * 60 * 72) - dur;
                  if (dur > 259200) {
                    miData.showTimer = true;
                  }
                }

                if (miData.showTimer) {
                  tiempo = dur
                }
                miData.tiempo = tiempo;
                miData.prio = prio;


                this.actividades.push(miData)

              }

            }
          } else {
            this.actividades.push(miData)
          }




        }






      }

      this.actividades = _.sortBy(this.actividades, 'prioridad')
      this.cargaActividades = true;

      this.click = false;

      console.log(this.actividades)



    })
  }

  cambio(event) {
    this.txt = event.detail.value;
  }

  async handleRefresh(event) {

    await this.cargarData();
    this.filtro = 'Todos'
    this.click = false;
    event.target.complete();
  }

  color(name, ic?) {

    if (name == 'MANTENIMIENTO FINALIZADO') {
      return ic ? 'iverde' : 'verde'
    }

    if (name == 'SOLICITUD ENVIADA AL TECNICO') {
      return ic ? 'iamarillo' : 'amarillo'
    }

    if (name == 'AREA DE MANTENIMIENTO') {
      return ic ? 'irojo' : 'rojo'
    }



  }

  async filtros(event) {

    const miPopover = await this.popover.create({
      component: OpcionesCDOComponent,
      componentProps: {
        opciones: this.unicos
      },
      event: event
    })

    await miPopover.present();

    const dataResume: any = await miPopover.onWillDismiss();

    if (dataResume.data) {
      this.filtro = dataResume.data.by;
    }

  }



}
